$global-font-icon: 'social';
@font-face {
  font-family: $global-font-icon;
  src:url("../assets/fonts/myicons.eot");
  src:url("../assets/fonts/myicons.?#iefix") format("embedded-opentype"),
    url("../assets/fonts/myicons.woff") format("woff"),
    url("../assets/fonts/myicons.ttf") format("truetype"),
    url("../assets/fonts/myicons.#myicons") format("svg");
  font-weight: normal;
  font-style: normal;
}

.icon {
  font-family: $global-font-icon;

  &:before {
    /* Some common icon stuff */
  }

  &.icon--apple {
    &:before {
      content: $icon-apple;
    }
  }
  &.icon--check {
    &:before {
      content: $icon-check;
    }
  }
  &.icon--logo {
    &:before {
      content: $icon-logo;
    }
  }
  &.icon--windows {
    &:before {
      content: $icon-windows;
    }
  }

}